@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.8.1/font/bootstrap-icons.css");

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background-color: rgb(35, 35, 35);
    min-height: 100vh;
    padding: 20px;
    font-family: "Heebo", sans-serif;
}

.card {
    max-width: 650px;
    margin: auto;
    overflow: hidden;
    background-color: rgb(20, 24, 28);
    border-radius: 10px;
    color: white;
}

.card-header {
    width: 100%;
    text-align: center;
    margin-bottom: 25px;
}
.img-wrapper {
    width: 100%;
    overflow: hidden;
    height: 300px;
}

.card-header img {
    width: 100%;
    object-fit: cover;
    object-position: center;
    height: 100%;
    transition: all 1s ease-in-out;
}
.card-header img:hover {
    transform: scale(1.2);
}

.wrapper {
    padding: 0 50px;
}
.card-header .info {
    margin: 15px 0;
    line-height: 1.2;
}

.card-header .info h3 {
    font-size: 38px;
}

.card-header .info p {
    font-size: 20px;
}

.buttons {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    align-items: center;
    justify-content: center;
}

.buttons button {
    flex: 1;
    padding: 10px;
    font-size: 24px;
    font-weight: bold;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    transition: 0.3s all ease-in-out;
}

.email-button {
    background-color: white;
    color: black;
}
.email-button:hover {
    background-color: black;
    color: white;
}
.buttons i {
    margin-right: 10px;
}

.linkedIn-button {
    background-color: rgb(13, 94, 187);
    color: white;
}

.linkedIn-button:hover {
    background-color: white;
    color: rgb(13, 94, 187);
}

/* Card Second part */

.card-body .wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 25px;
}

.card-body h4 {
    font-size: 26px;
}

.icons {
    margin: 20px 0;
    font-size: 26px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.icons i {
    cursor: pointer;
}
